

.portfolio-entry {
	--base-duration: .3s;
	
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	flex:1 1 100%;
	
	@include breakpoint-up(xlarge) {
		flex:0 0 50%;
	}
	width:100%;
	min-height: 50vh;
	
	background-position: center;
	background-size: cover;

	transition-timing-function: ease-in-out;
	transition-duration: var(--base-duration);

	.discover {
		font-size:1rem;
		
		line-height:1.35rem;
		padding:0rem 1rem;
		border: 2px solid $white;
		border-radius:0.5rem;	
		transform: scale(0.5);	
		opacity: 0;
		transition: var(--base-duration) cubic-bezier(0.65, 0, 0.35, 1);
		max-height:0;
		margin-top:0m;
		cursor:pointer;

		&:hover, &:active {
			background-color: $white;
			color:$offblack;			
		}
	}

	.body {
		color:$lightergrey;
		width:80%;
		text-align: left;
		transform: scale(0.5);	
		opacity: 0;
		transition: var(--base-duration) cubic-bezier(0.65, 0, 0.35, 1);
		max-height:0;

		border-left:2px solid $white;
		padding:0rem 1rem;

		@include breakpoint-up(xlarge) {
			width:50%;
		}

		p:last-of-type {
			margin-bottom: 0;
		}
	}

	&:hover, &:active {
		backdrop-filter: greyscale(100);
		
		transition: 0.15s cubic-bezier(0.65, 0, 0.35, 1);

		& .portfolio-entry-overlay {
			& img {
				filter: blur(0.5rem) grayscale(100%) brightness(0.3);	
				transform: scale(1.05);
			}
		}

		& .portfolio-entry-title {		
			bottom: 50%;
			transform:translate(0, 50%);
		}

		& .discover {
			opacity: 1;
			transform: scale(1);	
			max-height:4rem;	
			padding:0.5rem 1rem;
			margin-top:2rem;
		}

		& .body {
			opacity: 1;
			transform: scale(1);				
			max-height:300px;
			padding:.5rem 1rem;
		}
	}

	&-overlay {
		position: absolute;
		inset: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: var(--base-duration) cubic-bezier(0.65, 0, 0.35, 1);
		overflow: hidden; 

		
		
		.img-overlay {
			position: absolute;
			inset: 0;
			top:50%;
			background: linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.5));
		}
		
		& img {
			transform: scale(1);
			transition-timing-function: ease-in-out;
			transition-duration: 0.25s;
		}
	}

	&-title {
		position: absolute;
		text-align: center;
		bottom: 0rem;
		padding:1rem;
		transform:translate(0, 0);
		transition: var(--base-duration) cubic-bezier(0.65, 0, 0.35, 1);

		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		
		@include breakpoint-up(medium) {
			bottom: 2rem;
		}

	}

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	h1 {
		color: $white;
		font-weight: 100;
		letter-spacing: normal;
		text-transform: none;

		font-size: 2rem;
		line-height: 2.5rem;

		@include breakpoint-up(medium) {
			font-size: 4rem;
			line-height: 4.5rem;
		}
	}



	h2 {
		font-size: 2rem;
		color: $lightergrey;

		font-weight: 100;
		letter-spacing: normal;
		text-transform: none;

		font-size: 1rem;
		line-height: 1.5rem;
		
		@include breakpoint-up(medium) {
			font-size: 1.5rem;
			line-height: 2rem;
		}
	}

	.button {

		text-transform: uppercase;
		font-weight: 700;
		transform: translateX(-1rem);
		margin-top: 0.5rem;


		@include breakpoint-up(large) {
			font-size: 1.2rem;
			
			padding-left: 2rem;
			transform: translateX(-2rem);
		}


		float:left;
		padding:1rem;
		color:$lightgrey;
		display:flex;
		align-items: center;

		transition-timing-function: ease-in-out;
		transition-duration: 0.15s;

		cursor:pointer;

		svg {
			fill: $white;
			max-height: 3rem;
			max-width: 3rem;
			padding-left: 1rem;
		}

		&:hover, &:active {
			color: $white;

			&>span {
				margin-left: 0.75rem;
				color: $dsblue;
			}
		}

		>span {
			margin-left: 0.5rem;

			transform: scale(1.5);
			transition-timing-function: ease-in-out;
			transition-duration: 0.15s;
		}
	}
}

.mouse-over-screen {
	position:absolute;
	inset:0;
	z-index:100;
	opacity:0.1;
	mix-blend-mode: screen;
	transition-duration: 150ms;

	&:hover {
		opacity:0.5;
	}
}

.full-width-video {
	width:100%;
}